import fetch from '../utils/fetch';

window.$bid = getQueryString('bid') || 51;
const gobalBid = window.$bid || process.env.BID; // bidbidbid
const externalBid = process.env.EXTERNAL_BID; // bid
const testApi = process.env.TEST_API; // 
const onLineApi = process.env.ONLINE_API; // 
const payURL = process.env.PAY_URL; // 
const globalIstest = process.env.IS_TEST; // yesno
const apiEnv = process.env.API_ENV; // testproduct
const gobalWithCredentials = process.env.WITH_CREDENTIALS; // cookie

function getQueryString(name) {
  const reg = new RegExp(`(^|&)${name}=([^&]*)(&|$)`, 'i');
  const hash = window.location.hash.slice(window.location.hash.indexOf('?'));
  console.log('hash', hash);
  const r = hash.substr(1).match(reg);
  console.log('search', window.location.search);
  if (r != null) {
    return unescape(r[2]);
  }
  return null;
}

